@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap);
 html , body {
     background: linear-gradient(270deg, #ef32e9 0%, rgba(0,212,255,1) 100%);
}
* {
	font-family: 'Quicksand', sans-serif;
}
 h1 {
	-webkit-animation: fade ease 1s;
	        animation: fade ease 1s;
     font-weight: bold;
     color: white;
     text-align: center;
     font-size: 40px;
}
  p{
	-webkit-animation: fade ease 1s;
	        animation: fade ease 1s;
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    color: white;
}
 .center {
     position: absolute;
     top: 0;
     bottom: 0;
     left: 0;
     right: 0;
     width: 50%;
     height: 57%;
     margin: auto;
}
 .footer {
    font-weight: bolder;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    color: white;
    text-align: center;
    -webkit-animation: fade ease 1s;
            animation: fade ease 1s;
}
 @-webkit-keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}
 @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}
 header * {
    display: flex;
}
 header {
     position: fixed;
     top: 0;
     left: 0;
     right: 0;
     height: 5rem;
     display: flex;
     align-items: center;
}
 header img {
     padding-left: 1rem;
     width: 4rem;
     -webkit-animation: fade ease 1s;
             animation: fade ease 1s;
}
 header a {
     font-size: 1rem;
     color: white;
     text-decoration: none;
     margin: 20px;
     -webkit-animation: fade ease 1s;
             animation: fade ease 1s;
     font-weight: bolder;
}
 .link{
    display: inline-block;
}
 .link::after{
     content: "";
     display: block;
     width: 0;
     height: 2px;
     background: #ffffff;
     transition: width .3s;
}
 .link:hover::after{
     width: 100%;
     transition: width .3;
}
 .button {
     background: linear-gradient(270deg, #ff5f6d 0%, #ffc371 100%);
     border: 0;
     padding: 10px;
     font-size: 16px;
     font-weight: bolder;
     cursor: pointer;
     border-radius: 3px;
     position: relative;
     left: 50%;
     color: white;
     transform: translate(-50%, -50%);
     -webkit-animation: fade ease 1s;
             animation: fade ease 1s;
     text-align: center;
}
 .fbutton {
     background: linear-gradient(270deg, #ff5f6d 0%, #ffc371 100%);
     border: 0;
     padding: 10px;
     font-size: 16px;
     font-weight: bolder;
     cursor: pointer;
     border-radius: 3px;
     position: relative;
     left: 50%;
     color: white;
     transform: translate(-50%, -50%);
     -webkit-animation: fade ease 2s;
             animation: fade ease 2s;
     text-align: center;
}
 .fa{
     color: white;
     text-decoration: none;
     padding-left: 8px;
     padding-right: 8px;
     transition: .5s;
}

.cpr{
  transition: .5s;
  cursor: default;
}

.fa-github:hover{
  color: #3cffa4;
}

.fa-twitter:hover{
  color: #3cdbff;
}

.fa-reddit:hover{
  color: #ff5c5c;
}

.fa-envelope:hover{
  color: #ce5cff;
}

.cpr:hover{
  color: #5cfcff;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

